/* STRIPPING STYLES -------------------------------------------------*/
html,
body, 
img, 
h1, 
h2, 
h3, 
h4,
a, 
p, 
ul, 
li,
button {
  padding: 0;
  margin: 0;
}

body {
  background-image: url('https://drive.google.com/uc?export=view&id=1rTADLiO17cPnC9r2-h6awIfYiw4RrMbu');
  background-size: contain;
  background-repeat: repeat;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
}


/* GLOBAL STYLES -----------------------------------------------------*/
html {
  font-size: 62.5%;
}

h1, 
h2,
h3,
h4,
p,
a,
button{
  color: #303030;

  font-family: goudy-old-style, serif;
  text-transform: lowercase;
  letter-spacing: 2px;
  font-weight: 400;
}

h1 {
  font-size: clamp(3rem, 10vw, 6rem);

  padding: 150px 0 5px 0;
}

h2 {
  font-size: clamp(2rem, 8vw, 5rem);
  
  padding: 20px 0;
}

h3 {
  font-size: clamp(1.8rem, 6vw, 4rem);
}

h4 {
  font-size: clamp(1.8rem, 2vw, 2rem)
}

h3,
h4,
p {
  font-style: italic;
}

p,
a,
button {
  font-size: clamp(1.6rem, 3vw, 2rem);
}

button {
  background-color: #fffcf5;
  
  padding: 15px 25px;
  margin: 10px 0;

  cursor: pointer;
  transition: all .4s;
}

button:hover,
button:focus {
  background-color: #fffcf585;
}

img {
  width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1080;
  width: 85%;
  margin: auto;
}

.skip-link {
  background-color: #ddd;
  
  text-align: center;
  font-size: clamp(1.8rem, 4vw, 3rem);

  border-radius: 4px;
  width: 225px;
  padding: 5px;

  position: absolute;
  left: -1000px;
  top: 0;
  z-index: 30;
}

.skip-link:focus {
  left: 0;
}

/* NAVIGATION STYLES -------------------------------------------------*/
.nav {
  background-color:#fffcf580;

  position: sticky;
  top: 0;
  z-index: 10;

  overflow: hidden;
}

.flex-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav ul {
  display: flex;
  justify-content: space-between;
}

nav a {
  letter-spacing: 3px;
  text-transform: uppercase;

  padding: 10px 20px 5px;
}

nav a:hover,
nav a:focus,
.footer-link:hover,
.footer-link:focus {
  border-bottom: 1px solid #30303090;
} 

.nav-icons{
  display: flex;
}

.nav-icons button {
  background: none;

  font-size: 2rem;
}

.icon-button {
  opacity: .85;

  padding: 10px;

  transition: all .4s;
}

.icon-button:hover,
.icon-button:focus{
  background: none;

  opacity: .7;
}


/* HEADER STYLES -----------------------------------------------------*/
header {
  height: 60vh; 
}

.flex-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sunday {
  font-family: ltc-goudy-oldstyle-pro, serif;
  font-style: italic;
  letter-spacing: 3px;
}

.homework {
  font-weight: 500;
}

.tagline {
  font-size: clamp(1.7rem, 4vw, 2.5rem);
}

/* WISHLIST STYLES ---------------------------------------------------*/
.wishlist {
  background-image: url('https://drive.google.com/uc?export=view&id=1NkeemhFav0YXOoyG8uN4341pm5VIhZg6');
  background-size: cover;
  background-repeat: no-repeat; 

  border-radius: 10px 0 0 10px;
  width: 50%;
  padding: 15px 30px 60px; 
  height: 100%;

  position: fixed;
  top: 0;
  right: 0;
  z-index: 20;

  overflow-y: scroll; 
} 

.wishlist-info {
  padding: 50px 20px 20px 165px;
}

.exit-button,
.close-modal {
  background: none;

  font-size: clamp(2.6rem, 3vw, 3.4rem);
}

.exit-button {
  position: absolute;
  top: 5%;
  left: 8%;
}

.wishlist-flex {
  padding: 20px 0;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.wishlist-flex img {
  width: 30%;
}

.wishlist-flex p {
  width: 50%;
}

.wishlist-flex button {
  background: none;

  font-size: clamp( 2.2rem, 3vw, 3.6rem);
}


/* MODAL STYLES  -----------------------------------------------------*/
.modal {
  background-color: #30303090;

  width: 100%;
  height: 100%; 
  padding-top: 150px;

  position: fixed; 
  left: 0; 
  top: 0;
  bottom: 0;
  z-index: 100;
}

.modal-content {
  background-color: #FFFCF5;

  border-radius: 10px;
  padding: 20px; 
  margin: auto; 
  width: 30%;  
  height: 30%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  position: relative; 

  animation-name: animatetop;
  animation-duration: 0.4s
}

.modal-content h3 {
  letter-spacing: 3px;

  padding-top: 20px;
}

.modal-content p {
  font-style: normal;
}

.modal-content p span {
  color: #C84646;

  font-weight: 500;
  font-style: italic;
  font-size: clamp(1.8rem, 2vw, 2.7rem);
}

.close-modal {
  margin: 10px;

  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animatetop {
  from {
    top: -300px; 
    opacity: 0;
  }

  to {
    top: 0; 
    opacity: 1;
  }
}

/* ITEM STYLES ----------------------------------------------------- */
.items {
  background-color: #ddd;

  text-align: center;
 
  padding: 60px 0;
}

.item-img {
  padding-bottom: 10px;

  position: relative;
}

.star-sticker {
  color: #ffd5a4;

  font-size: clamp(2.6rem, 3vw, 3.4rem);

  position: absolute;
  top: 3%;
  right: 5%;
}

.item h3 {
  font-size: clamp(1.8rem, 2vw, 2.4rem);
}

.items p {
  padding-top: 5px;
}

.items ul {
  padding: 80px 0;
  margin: auto;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 60px;
}


/* FOOTER STYLES ----------------------------------------------------- */
footer {
  background-color: #fffcf5;

  padding: 30px 0;
}

footer p {
  font-style: normal;

  padding: 3px 0;
}

.footer-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-link {
  padding: 0 5px;
}

.icons {
  font-size: clamp(1.8rem, 4vw, 3rem);
  text-align: center;

  padding: 0 10px;
}


/* MEDIA QUERIES  -----------------------------------------------------*/

/* portrait tablet + desktop */
@media(max-width: 940px) {
  header {
    height: 40vh;
  }

  .wishlist {
    width: 70%;
  }
}

/* landscape phone + protrait tablet */
@media(max-width: 768px) {

  body {
    background-image: url('https://drive.google.com/uc?export=view&id=1z7YtycSIP1xv-XkOnYczmfZDsv5Q6LKj');
  }

  nav a:hover,
  nav a:focus,
  .footer-link:hover,
  .footer-link:focus {
    border-bottom: none;

    font-weight: 600;

    padding-bottom: 0;
  }

  .flex-header {
    padding: 20px 0;

    flex-direction: column;
  }

  .wishlist {
    width: 100%;
  }

  .modal-content {
    width: 50%;
    height: 30%;
  }

  .items ul {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
  }
 
  .footer-flex {
    flex-direction: column;
    text-align: center;
  }

  .footer-text {
    padding-top: 20px;
    order: 1;
  }
}

/* landscape phones + down */
@media(max-width: 480px) {

  .wrapper {
    width: 95%;
  }

  .flex-header {
    flex-direction: row;
  }

  .header-text {
    padding: 0 5px 20px;
  }

  h1 {
    padding-top: 120px
  }

  nav ul {
    flex-direction: column;
  }

  .wishlist-info {
    padding: 40px 15px 20px;
  }

  .exit-button {
    top: 2%;
  }

  .wishlist-flex img {
    padding: 5px;
  }

  .modal-content {
    width: 70%;
    height: 30%;
  }

  .items ul {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    gap: 60px;
  }

  .item {
    margin: 0 20px;
  }
}

